html {
  font-family: Arial, Helvetica, sans-serif;
}

h1, h2, p {
  margin: 0;
}

.hidden {
  display: none;
}

.cat-info {
  justify-content: center;
  display: flex;
}

.cat-info__content {
  justify-content: space-between;
  gap: 60px;
  width: 900px;
  margin-top: 20px;
  display: flex;
}

.cat-info__image-container {
  border-radius: 8px;
  width: 50%;
  height: 200px;
  overflow: hidden;
}

.cat-info__image {
  object-fit: cover;
  object-position: center;
  width: 100%;
  height: 100%;
  transition: scale .3s ease-out;
}

.cat-info__image:hover {
  scale: 1.05;
}

.cat-info__text-block {
  width: 70%;
}

.cat-info__name, .cat-info__description {
  margin: 0 0 24px;
}
